@import url(https://fonts.googleapis.com/css2?family=Anton&family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Lobster&display=swap);

  .ant-layout {
    height: 100%;
     }
  Content{
      height: 100%;
      
      
  }

  .bgimage{
   
    background-position: center;
    width: 100vw;
    
    height: 100vh;
  }
  .antheader{
  
    background-color: lavenderblush;
    
    height: 40vh;
  }
.card{
    display: flex;
    justify-content: space-between;
    height: 80%;
    flex-wrap: wrap;
    width: 100%;
    
}

.imageCard{
    border: 3px solid rgb(54, 53, 53);
    width: 18%;
    height: 0%;
    background-color: white;
    color: black;
    margin-bottom: 20px;
    text-align: center;
}
.imageCard img{
    width: 100%;
    height: 33vh;
}

@media only screen and (max-width: 500px){
.card{
justify-content: center;
height: 80%;
} 

.imageCard{
width: 80%;   
}

}
body{
    margin: 0;
    padding: 0;
}


.property{
    font-size: 1rem;
    font-weight: 400;
    font-family: 'Lobster', cursive;
    display: flex;
    width: 100%;
    height: 10%;
    background-color: rgb(252, 238, 238);
    color: rgb(1, 7, 1);
    border: rgb(59, 180, 106);

}



@media only screen and (max-width: 500px){

.property{
    height: 10%;
}


}
.header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Lobster', cursive;
    font-size: 1.2rem;
    background-color: rgb(28, 4, 66);
    color: white;
    height: 10vh;
    position: -webkit-sticky;
    position: sticky;
}
a{
    color: white;
}
.icon{
    width: 10%;
    height: 80%;
    display: flex;
    justify-content: center;
}

@media only screen and (max-width: 500px){
.header{
    height: 30vh;
    flex-direction: column;
}
.icon {
    display: flex;
    justify-content: left;
    width: 25%;
    margin-left: 35px;
}


}
.intro{
    background-color: rgb(250, 227, 227);
   
    justify-content: center;
     padding: 0px;
  width: 100%;
  height: 400%;
  object-fit: cover;
  box-sizing: border-box;
  color: rgb(255,255,255);
  font-family: 'Lobster', cursive;
  overflow: auto;
  float:left;

}





.footer{
    width: 100%;
    height: 40%;
    background-color: rgb(20, 18, 18);
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: white;
    font-size: 1.3rem;
    font-weight: 600;
    font-family: 'Lobster', cursive;

}





@media only screen and (max-width: 500px){
.footer{
    flex-direction: column;
}


}
.main{
    height: 100%;
    width: 100%;
    color: rgb(10, 10, 10);
    background-color: rgb(212, 158, 158);
}
.main h1{
    color: rgb(8, 8, 8);
    font-size: 1.4rem;
}
.img img{
    height: 5%;
    width: 20%;
}

@media only screen and (max-width: 500px){

.main{
    font-size: 0.7rem;
    line-height: 13px;
    justify-content: space-around;
    margin-right: 10px;
}

.main h1{
    font-size: 0.9rem;
}


}
